                    thisSelection
                        .classed("show", true)
                        .classed("hide", false)
                        .style("fill", legendItem.color);
                    legendItem.selection
                        .classed("show", true)
                        .classed("hide", false)
                        .style("fill-opacity", 1)
                        .style("stroke-opacity", 1);
